import { Component, OnInit } from '@angular/core';
import { WordpressService } from './../../providers/word-press/word-press';
import { LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-feed',
  templateUrl: 'feed.page.html',
  styleUrls: ['feed.page.scss']
})
export class FeedPage {

 //categories = ['India','World','US','Croatia','Phillipines','Russia'];
 categories = [];
 categoryID = 0;
 postsPerCat = [];
 page = 1;
 count = null;
 selectedIdx = 657;
 isFirst = false;
 first: boolean;

  constructor(private wp: WordpressService, private loadingCtrl: LoadingController) {}

  ngOnInit() {
    this.getCategories();
    console.log(this.categories);
  }

  async getCategories(){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    return this.wp.getAllCategories().subscribe(res => {
      this.categories = res;
      console.log(this.categories);
      this.getPostByCategories(this.categories[0].id);
      loading.dismiss();
    });
  }

  async getCatByID(id){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.wp.getCathegory(id).subscribe(res => {
      this.categories = res;
      console.log(this.categories);
      loading.dismiss();
    });
  }

  async getPostByCategories(id){
    let loading = await this.loadingCtrl.create({
      message: 'Loading Data...',
      translucent: true,
      cssClass: 'custom-loading'
    });
    await loading.present();

    this.first = false;
    this.isFirst = false;
    this.wp.getPostsByCategory(id).subscribe(res => {
      this.postsPerCat = res;
      this.count = this.wp.totalPosts;
      //console.log(this.postsPerCat);
      //console.log(this.categories);
      loading.dismiss();

    });
  }

  loadMore(event) {
    this.page++;

    this.wp.getPostsByCategory(this.page).subscribe(res => {
      this.postsPerCat = [...this.postsPerCat, ...res];
      event.target.complete();
        console.log(this.postsPerCat);
      // Disable infinite loading when maximum reached
      if (this.page == this.wp.pages) {
        event.target.disabled = true;
      }
    });
  }


}
